




























import {Vue, Component, Prop} from 'vue-property-decorator';
import services from '@/services';

@Component
export default class MoreSetting extends Vue {
  @Prop()
  private editObj: any;

  private formData: any = {
    name: '',
    slug: '',
    description: '',
    meta_title: '',
    meta_description: '',
  };
  private doLoading: boolean = false;
  private mounted() {
    if (this.editObj) {
      Object.assign(this.formData, this.editObj);
    }
  }
  private async onSubmit() {
    console.log('submit...');
    try {
      this.doLoading = true;
      if (this.editObj) {
        await services.tagService.update(this.formData);
        this.$message.success('更新成功！');
      } else {
        await services.tagService.create(this.formData);
        this.$message.success('创建成功！');
      }
      this.closeModal();
      this.$emit('refreshList');
    } catch (err) {
      this.$message.error(err.message);
    } finally {
      this.doLoading = false;
    }
  }
  private closeModal() {
    this.$emit('close');
  }
}
